"use client"
import { Button } from "@/components/ui/button"
import { Menu } from "lucide-react"
import Link from "next/link"
import { useState } from "react"

const Header = () => {

  const [open, setOpen] = useState(false)

const navItems = [
  { name: 'Beneficii', href: "#beneficii"},
  { name: 'Nutritie', href: "#nutritie"},
  { name: 'FAQ', href: "#faq"},
  { name: 'Oferta', href: "#oferta"},
]

  return (
    <>
      <header className="sticky top-0 z-50 w-full border-b border-border/40 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-5">
          <div className="flex items-center justify-between mx-5 lg:mx-0 ">
            <div className="flex items-center gap-2">
              Acitveboost Logo
            </div>
              
                {/* Desktop Nav */}
        <nav className="hidden md:flex items-center gap-8">
           { navItems.map((navItem) =>(

            <div>
            <button className="text-slate-300 hover:text-slate-200 cursor-pointer" key={navItem.name}>
              {navItem.name}
            </button>
               
 
              </div>
           ) )}
        </nav>

  {/* Desktop Nav */}


             <Menu className="flex lg:hidden md:hidden"/>
          </div>
        </div>
      </header>
    </>
  )
}

export default Header